<template>
  <div class="add">
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form :model="form">
        <el-form-item label="所属角色" :label-width="formLabelWidth">
          <el-select v-model="form.roleid">
            <el-option label="" value="" disabled>--请选择--</el-option>
            <!-- value值为变量 -->
            <el-option v-for="item in list" :key="item.id" :label="item.rolename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="用户名" :label-width="formLabelWidth">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" :label-width="formLabelWidth">
          <el-input type="password" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="菜单状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="blue"
            inactive-color="grey"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="confirm"  v-if="info.isAdd">确 定</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>


<script>
import {mapGetters,mapActions} from 'vuex'
import {addManager,getManager,editManager} from '../../../utils/request'
import { successAlert } from '../../../utils/alert'
export default {
  props:["info"],
  data(){
    return {
      form:{
        roleid:'',
        username:'',
        password:'',
        status:1,
      },
      formLabelWidth:'80px',
    }
  },
  computed:{
    ...mapGetters({
      "list":"role/roleList"
    })
  },
  methods:{
    ...mapActions({
      "requestRoleList":"role/roleListActions",
      "requestManagerList":"manager/managerListActions",
      "requestCount":"manager/countActions",
    }),
    cancel(){
      this.info.show = false;
      this.form = {
        roleid:'',
        username:'',
        password:'',
        status:1,
      }
    },
    confirm(){
      addManager(this.form).then(res=>{
        successAlert(res.data.msg);
        this.cancel()
        this.requestCount()
        this.requestManagerList()
      })
    },
    getDetail(id){
      getManager({uid:id}).then(res=>{
        this.form = res.data.list;
        this.form.password = '';
      })
    },
    update(){
      editManager(this.form).then(res=>{
        successAlert(res.data.msg);
        this.cancel()
        this.requestManagerList()
      })
    }
  },
  mounted(){
    this.requestRoleList()
  }
}
</script>

<style>

</style>



