<template>
  <div class="list">
    <el-table
      :data="managerList"
      style="width: 100%;margin-bottom: 20px;"
      row-key="id"
      border
      :tree-props="{ children: 'children' }"
    >
      <el-table-column prop="id" label="用户编号"></el-table-column>
      <el-table-column prop="username" label="用户名"></el-table-column>
      <el-table-column prop="rolename" label="所属角色"> </el-table-column>
      <el-table-column prop="status" label="状态">
        <template v-slot="scope">
          <el-button type="primary" v-if="scope.row.status == 1"
            >启用</el-button
          >
          <el-button type="danger" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template v-slot="scope">
          <el-button type="primary" @click="edit(scope.row.uid)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.uid)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background layout="prev, pager, next" :page-size="2" @current-change="cPage" :total="count" v-if="count">
    </el-pagination>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import {delManager} from '../../../utils/request'
import { successAlert } from '../../../utils/alert';
export default {
  computed: {
    ...mapGetters({
      managerList: "manager/managerList",
      count: "manager/count"
    })
  },
  methods: {
    ...mapActions({
      requestManagerList: "manager/managerListActions",
      requestCount: "manager/countActions",
      "requestPage":"manager/pageActions",
    }),
    //
    cPage(page){
      this.requestPage(page)
      this.requestManagerList()
    },
    edit(id){
      this.$emit("edit",id)
    },
    del(id){
      this.$confirm('确定要删除吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            delManager({uid:id}).then(res=>{
              successAlert(res.data.msg);
              this.requestCount()
              this.requestManagerList()
      }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      })



    }
  },
  mounted() {
    this.requestCount();
    this.requestManagerList();
  }
};
</script>

<style></style>



